$color_1: #242831;
$color_2: #16191e;
$color_3: black;
$color_4: #18f08b;
$font-family_1: "Rubik", sans-serif;
$background-color_1: #CAF4D8;
$background-color_2: #BBF8BE;
$background-color_3: rgba(255, 255, 255, 0.15);

.glass{
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0));
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 20px;
  border:1px solid rgba(255, 255, 255, 0.18);
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
} 
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: $font-family_1;
}
body {
	height: 100vh;
	background: linear-gradient(108.9deg, rgb(163, 217, 221) 10.4%, rgb(174, 250, 182) 56.2%, rgb(238, 243, 220) 86.1%);
	display: flex;
	justify-content: center;
	align-items: center;
}
.calculator__container {
	width: 90vw;
	max-width: 37.5em;
	// background: radial-gradient(circle at 10% 20%, rgba(249, 251, 250, 0.46) 0.1%, rgba(247, 246, 246, 0.28) 90.1%);
	padding: 6em 3em;
	border-radius: 20px;
}
.calculator__inp-wrapper {
	display: flex;
	justify-content: space-around;
	gap: 1.2em;
}
.calculator__end-date-wrapper {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 400px;
	height: max-content;
	input[type="radio"] {
		opacity: 0;
		/* position: fixed; */
		width: 0;
	}
	label {
		display: flex;
		background-color: #ddd;
		// font-family: sans-serif, Arial;
		// font-size: 16px;
		// border: 2px solid #444;
		justify-content: space-between;
		align-items: center;
		border-radius: 4px;
		height: max-content;
		margin-top: 0.6em;
		background-color: white;
		input[type='date'] {
			margin: 0;
			background-color: inherit;
		}
	}
	label:nth-of-type(2), label:last-of-type {
		padding: 10px 10px;
		background-color: transparent;
	}
	
	label:hover {
		background-color: #dfd;
		cursor: pointer;
	}
	
	input[type="radio"]:focus + label {
		// border: 1px solid white;
		// background-color:rgb(174, 250, 182);
		background-color: white;
	}
	
	input[type="radio"]:checked + label + input[type='date'] {
		// background-color: #bfb;
		background-color: white;
		// border-color: #4c4;
	}
}
.calculator__type-days {
	display: flex;
	justify-content: center;
	margin-top: 30px;
}
fieldset {
	border: none;
}
label {
	color: $color_1;
	display: block;
	font-weight: 600;
}
legend {
	color: $color_1;
	display: block;
	font-weight: 600;
}
input[type="date"] {
	font-size: 16px;
	padding: 1em;
	color: $color_1;
	border: none;
	outline: none;
	border-radius: 0.2em;
	margin-top: 0.6em;
	// background-color: transparent;
}
::-webkit-calendar-picker-indicator {
	background-color: $background-color_1;
	padding: 0.2em;
	cursor: pointer;
	border-radius: 0.1em;
}
.calculator__btn {
	display: block;
	background-color: $background-color_2;
	color: $color_2;
	font-size: 18px;
	margin: 1em auto 2em auto;
	border: none;
	padding: 0.7em 2em;
	border-radius: 0.3em;
	font-weight: 600;
}
#output {
	background-color: $background-color_3;
	text-align: center;
	padding: 1em;
	color: $color_3;
	font-weight: 600;
	font-size: 1.2em;
	letter-spacing: 0.05em;
	span {
		color: $color_4;
		font-size: 1.4em;
		font-weight: 600;
	}
}
@media screen and (max-width: 550px) {
	.container {
		padding: 4em 2em;
	}
	.inp-wrapper {
		flex-direction: column;
	}
	.date-wrapper {
		display: flex;
		align-items: center;
		flex-direction: column;
	}
}